.stage-cube-cont {
  width: 50%;
  height: 100%;
  top: 0;
  padding-top: 18%;
  margin-left: 0;
  position: absolute;
  right: 0;
  overflow: hidden;
}

.cubespinner {
  animation-name: spincube;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-duration: 12s;
  transform-style: preserve-3d;
  transform-origin: 100px 100px 0;
  margin-left: calc(50% - 100px);

  div {
    position: absolute;
    width: 200px;
    height: 200px;
    border: 1px solid #ccc;
    background: rgba(255, 255, 255, 0.4);
    text-align: center;
    font-size: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 20px 0px lightyellow;
  }

  .face1 {
    transform: translateZ(100px);
    color: #dd0031;
  }
  .face2 {
    transform: rotateY(90deg) translateZ(100px);
    color: #f06529;
  }
  .face3 {
    transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
    color: #28a4d9;
  }
  .face4 {
    transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
    color: #5ed4f4;
  }
  .face5 {
    transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
    color: #efd81d;
  }
  .face6 {
    transform: rotateX(-90deg) translateZ(100px);
    color: #ec4d28;
  }
}

@keyframes spincube {
  from,
  to {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
  }
  16% {
    transform: rotateY(-90deg);
  }
  33% {
    transform: rotateY(-90deg) rotateZ(90deg);
  }
  50% {
    transform: rotateY(-180deg) rotateZ(90deg);
  }
  66% {
    transform: rotateY(-270deg) rotateX(90deg);
  }
  83% {
    transform: rotateX(90deg);
  }
}

// Add these responsive styles at the end of your About scss file

@media screen and (max-width: 1200px) {
  .stage-cube-cont {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 50%;
    margin-top: 80px;
    overflow: visible;
    margin-bottom: 100px;
  }
  
  .cubespinner {
    top: -60px;
    
    div {
      font-size: 70px;
      width: 140px;
      height: 140px;
    }
    
    .face1 { transform: translateZ(70px); }
    .face2 { transform: rotateY(90deg) translateZ(70px); }
    .face3 { transform: rotateY(90deg) rotateX(90deg) translateZ(70px); }
    .face4 { transform: rotateY(180deg) rotateZ(90deg) translateZ(70px); }
    .face5 { transform: rotateY(-90deg) rotateZ(90deg) translateZ(70px); }
    .face6 { transform: rotateX(-90deg) translateZ(70px); }
    
    margin-left: calc(50% - 70px);
  }
}

@media screen and (max-width: 480px) {
  .cubespinner {
    div {
      font-size: 60px;
      width: 120px;
      height: 120px;
    }
    
    .face1 { transform: translateZ(60px); }
    .face2 { transform: rotateY(90deg) translateZ(60px); }
    .face3 { transform: rotateY(90deg) rotateX(90deg) translateZ(60px); }
    .face4 { transform: rotateY(180deg) rotateZ(90deg) translateZ(60px); }
    .face5 { transform: rotateY(-90deg) rotateZ(90deg) translateZ(60px); }
    .face6 { transform: rotateX(-90deg) translateZ(60px); }
    
    margin-left: calc(50% - 60px);
  }
}