$primary-color: #ff4500;

@import 'animate.css';
@import '~loaders.css/src/animations/pacman.scss';

@font-face {
  font-family: 'Helvetica Neue';
  src: url('./assets/fonts/helvetica-neu.ttf') format('ttf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'La Belle Aurore';
  src:
    url('./assets/fonts/LaBelleAurore.woff2') format('woff2'),
    url('./assets/fonts/LaBelleAurore.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Coolvetica';
  src:
    url('./assets/fonts/CoolveticaRg-Regular.woff2') format('woff2'),
    url('./assets/fonts/CoolveticaRg-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

input,
textarea {
  font-family: 'Helvetica Neue', sans-serif;
}

.loader-hidden {
  display: none;
}

.loader-active {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  width: 50px;
  height: 50px;
  animation: fadeOut 1s 1s;
  animation-fill-mode: forwards;
}

html, body {
  overflow-x: hidden;
}

@media screen and (max-width: 1200px) {
  body {
    overflow: visible;
  }
  
  .loader-active {
    position: fixed;
  }
}

@media screen and (max-width: 768px) {
  .page {
    position: initial;
  }
}

:root {
  --app-height: 100vh;
}

@media screen and (max-width: 1200px) {
  .App {
    min-height: var(--app-height);
  }
  
  // Fix iOS 100vh issue
  @supports (-webkit-touch-callout: none) {
    .App {
      min-height: -webkit-fill-available;
    }
  }
}
